import { fieldValidation } from './fieldValidation';

export const fieldChangeHandler = (args, event, newValue) => {
  const { fieldName, formObject, changeType = 'none', toggleType = 'none' } = {
    ...args,
  };

  let updatedForm = {
    ...formObject,
    [fieldName]: {
      ...formObject[fieldName],
      value:
        changeType === 'change' && formObject[fieldName].dataType === 'array'
          ? newValue
          : changeType === 'change' &&
            formObject[fieldName].dataType === 'string'
          ? event.target.value
          : changeType === 'reset' &&
            formObject[fieldName].dataType === 'string'
          ? ''
          : formObject[fieldName].value,
      type:
        toggleType === 'passwordText' &&
        formObject[fieldName].type === 'password'
          ? 'text'
          : toggleType === 'passwordText' &&
            formObject[fieldName].type === 'text'
          ? 'password'
          : formObject[fieldName].type,
      validity: {
        ...formObject[fieldName].validity,
        ...fieldValidation({
          value:
            changeType === 'change' &&
            formObject[fieldName].dataType === 'array'
              ? newValue.length > 0
                ? newValue[newValue.length - 1]
                : ''
              : changeType === 'change' &&
                formObject[fieldName].dataType === 'string'
              ? event.target.value
              : changeType === 'reset' &&
                formObject[fieldName].dataType === 'string'
              ? ''
              : formObject[fieldName].value,
          validations: formObject[fieldName].validations,
        }),
      },
      icon: {
        ...formObject[fieldName].icon,
        isOn:
          toggleType === 'passwordText'
            ? !formObject[fieldName].icon.isOn
            : formObject[fieldName].icon.isOn,
      },
      isTouched: true,
    },
  };

  return updatedForm;
};
