import React from 'react';
import { SvgIcon, Grid } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';
import { makeStyles } from '@material-ui/core/styles';
import { Link as RouterLink } from 'react-router-dom';

import { addIcon } from '../../assets/svg/SvgIcons';
import CardList from '../Card/CardList';
import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({
  createBtn: {
    [theme.breakpoints.down('sm')]: {
      padding: `0 ${theme.spacing(2)} ${theme.spacing(2)}`,
    },
    [theme.breakpoints.up('sm')]: { padding: `0 ${theme.spacing(2)}` },
  },
}));

const TabCardContent = (props) => {
  const { cardData, cardType } = props;
  const [state, setState] = useStore();
  const classes = useStyles();
  return (
    <Grid container direction="column">
      <Grid
        container
        item
        justifyContent="flex-end"
        className={classes.createBtn}
      >
        <LoadingButton
          component={RouterLink}
          to={`/card`}
          startIcon={
            <SvgIcon>
              <path d={addIcon.path} />
            </SvgIcon>
          }
          color="secondary"
          pendingPosition="start"
          variant="contained"
          className={classes.buttonSpacing}
          type="submit"
        >
          {`Create ${cardType} Card`}
        </LoadingButton>
      </Grid>

      <Grid item style={{ margin: 'auto' }}>
        <CardList
          cardDataList={cardData.filter(
            (card) =>
              card.cardType === cardType && card.users._id === state.user._id
          )}
        />
      </Grid>
    </Grid>
  );
};

export default TabCardContent;
