import React, { useMemo, forwardRef } from 'react';
import {
  ListItem,
  ListItemText,
  SvgIcon,
  ListItemIcon,
  useMediaQuery,
} from '@material-ui/core';
import { useTheme } from '@material-ui/styles';
import { Link as RouterLink } from 'react-router-dom';

const CardMoreOption = (props) => {
  const { linkItem, moreOptionsHandler } = props;
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  const CustomLink = useMemo(
    () =>
      forwardRef((linkProps, ref) => (
        <RouterLink
          ref={ref}
          to={{ pathname: `/${linkItem.linkTo}`, state: linkItem.cardData }}
          {...linkProps}
        />
      )),
    [linkItem.linkTo, linkItem.cardData]
  );

  return (
    <ListItem
      button
      component={linkItem.type === 'link' ? CustomLink : null}
      divider
      dense={!isSmallDevice}
      onClick={linkItem.type === 'link' ? null : moreOptionsHandler}
    >
      <ListItemIcon>
        <SvgIcon>
          <path d={linkItem.svgPath} />
        </SvgIcon>
      </ListItemIcon>
      <ListItemText primary={linkItem.title} secondary={linkItem.description} />
    </ListItem>
  );
};

export default CardMoreOption;
